.card {
  font-family: "Agency FB";
  background: #dadada;
  border-radius: 5%;
  font-size: 40px;
  width: 470px;
  padding-bottom: 20px;
  display: inline-block;
  margin: 10px;
  border: 1px solid #666;
}

.card.big {
  zoom: 0.75;
}
.card.half {
  zoom: 0.5;
}
.card.quarter {
  zoom: 0.25;
}
.card.gold {
  background: #f1b500;
}

.card-id {
  padding: 10px;
  font-size: 30px;
  padding-right: 30px;
  text-align: right;
  font-weight: bold;
}

.card-img {
  margin-left: 20px;
  margin-right: 20px;
  width: 430px;
  height: 325px;
}

.card-title {
  height: 75px;
  line-height: 75px;
  padding-left: 20px;
  font-size: 40px;
  font-weight: bold;
}

.card-separator {
  padding-left: 90px;
  padding-right: 90px;
  height: 50px;
  padding-top: 25px;
  box-sizing: border-box;
}

.card-line {
  width: 100%;
  height: 1px;
  background: #e01c3c;
  -webkit-box-shadow: 0px 0px 15px 3px #e01c3c;
  -moz-box-shadow: 0px 0px 15px 3px #e01c3c;
  box-shadow: 0px 0px 15px 3px #e01c3c;
}

.card-description {
  width: 100%;
  padding: 20px;
  height: 180px;
  box-sizing: border-box;
}

.card-stars {
  height: 70px;
  text-align: center;
}

.card-star {
  width: 75px;
  height: 70px;
  background: url("../assets/star.png");
  display: inline-block;
}

.card.gold .card-star {
  background: url("../assets/star-gold.png");
}
