.card-modal {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, .5);
  z-index: 5;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.card-modal-container {
  margin: auto;
}

.card-modal-dependencies {
  width: 100%;
  overflow-x: auto;
  display: flex;
  padding: 10px;
  background: #333;
}
