.config {
  width: 100%;
  height: 30px;
  line-height: 30px;
  background: #333;
  color: white;
  display: flex;
  flex-direction: row;
  flex: 1;
  justify-content: space-evenly;
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
}

@media screen and (max-width: 800px) {
  .config {
    display: block;
    height: 90px;
  }
}

.config span {
  padding: 0 10px;
}
